        <HomeCenterNav />
        <!--  -->
        <HomeRecommendPlaylist />
        <!--  -->
        <HomeNewResource :data="homeData[3]" />
        <!--  -->
        <HomeLeaderboard />
        <!--  -->
        <HomeMusicCalendar :data="homeData[4]" />
        <!--  -->
        <HomeSelectedMusicVideo :data="homeData[5]" />
